.Introduction {
  width: 100%;
  height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #400080;
  justify-content: center;
  color: rgb(245, 237, 237);
  z-index: -9;
}
h1 {
  font-size: 4rem;
}
p {
  margin-top: 1rem;
  font-size: 2rem;
  text-transform: capitalize;
}
@media only screen and (max-width: 768px) {
 
  .Introduction {
    width: 100%;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #400080;
    justify-content: center;
    color: rgb(245, 237, 237);
    z-index: -9;
  }
  h1 {
    padding: 0rem 3rem 0rem 3rem;
    font-size: 3rem;
    text-align: center;
  }
  p {
    text-align: center;
    margin-top: 1rem;
    font-size: 1.8rem;
    text-transform: capitalize;
    padding-inline:2rem;
  }
}