.Collaboration
{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 50vh;
    border-bottom:1px dotted gray;
    background-color:#400080;
}
.Collaboration > .First
{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}
.Collaboration > .First >h1
{
    font-size: 4rem;
    font-weight: 300;
    margin-bottom: 2rem;
}
.Collaboration > .First >h3{
    text-align: center;
    font-size: 2rem;
    font-weight:100 ;
}
.Collaboration > .Second >a >button
{
  width: 35rem;
  padding: 2rem;
  border-radius: 2.5rem;
  font-size: 2rem;
  font-weight: 300;
  border: 2px solid white;
  background-color: #400080;
  color: white;
}
.Collaboration > .Second >a >button:active
{
    background-color: white;
    color: black;
    
    
}
.Footer {
  width: 100%;
  height: 55vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.Footer > .cote {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Footer > .cote > .Brand {
  font-size: 5rem;
  font-weight: bolder;
  font-family: "Comforter Brush", cursive;
  font-family: "MonteCarlo", cursive;
  color: #400080;
}
.Footer > .cote > .Detail {
  font-size: 2.2rem;
  text-align: center;
  font-weight: 200;
}
.Footer > .Intro2
{
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: space-around;
}
.Intro2 > img{
    width: 8rem;
    aspect-ratio: 1;
    margin-bottom: 2rem;
    
}
.Intro2 >h4{
    font-size: 1.5rem;
    font-style: italic;
    font-weight: 500;
}
.Exciting
{
    width: 70vw;
    height: 15rem;
   display: flex;
   justify-content: space-evenly;
     z-index: 1000; 
    place-items: center;
    background-color: rgb(26, 3, 63);
    margin-top: -10rem;
    border-radius: 1.78rem;
    box-shadow: 3px 3px 3px 0px rgb(26, 3, 63 ,0.6);
    
}
.Exciting > .Coute
{
    font-size: 3rem;
    font-weight: 500;
    color: white;
}
.Exciting > .ContactButton >a> button
{
 width: 15rem;
 padding: 1.5rem;
 font-size: 1.5rem;
 border: 2px solid white;
 border-radius: 5px;
 font-weight: bold;
 background-color:  rgb(26, 3, 63);
 color: white;
}
